<template>
  <div class="singer">
    <van-nav-bar left-text="返回" @click-left="goback" left-arrow />
    <div
      class="singer-top"
      :style="{ 'background-image': 'url(' + singerdetaildata.picUrl + ')' }"
    >
      <div class="singer-box">
        <div class="sing-title">
          <div class="singer-name">{{ singerdetaildata.name }}</div>
          <div class="singerNo">歌曲：{{ singerdetaildata.musicSize }}</div>
          <div>专辑：{{ singerdetaildata.albumSize }}</div>
        </div>
        <div>关注</div>
      </div>
    </div>
    <div class="singer-content">
      <van-tabs
        v-model="active"
        animated
        scrollspy
        @change="getsongerdatas"
        swipeable
      >
        <van-tab
          v-for="(item, index) in singertype"
          :key="index"
          :title="item.title"
        >
          <div class="types" v-show="active == 0">
            <div class="type-box">
              <div class="item-top">
                <div>近期热门</div>
                <div>更多</div>
              </div>
              <div class="item-box">
                <div
                  class="items"
                  v-for="(item, index) in singermusicdata"
                  :key="index"
                  @click="goPlayPage(item)"
                >
                  <div class="items-left">
                    <div class="items-img-box">
                      <img class="auto-img" :src="item.al.picUrl" />
                    </div>
                    <div class="item-text">
                      <div class="item-music-name van-ellipsis">
                        {{ item.name }}
                      </div>
                      <div class="item-name van-ellipsis">
                        <span v-for="(itm, ind) in item.ar" :key="ind">{{
                          itm.name
                        }}</span>
                        - {{ item.al.name }}
                      </div>
                    </div>
                  </div>
                  <div @click.stop="playmusic(item)" class="playbtn">
                    <van-icon name="play-circle-o" />
                  </div>
                </div>
              </div>
            </div>
            <div class="type-box">
              <div class="item-top">
                <div>歌手简介</div>
                <div>更多</div>
              </div>
              <div class="item-box textcolor van-multi-ellipsis--l7">
                {{ singerdetaildata.briefDesc }}
              </div>
            </div>
            <div class="type-box">
              <div class="item-top">
                <div>歌手简介</div>
                <div>更多</div>
              </div>
              <div class="item-box textcolor">
                {{ singerdetaildata.briefDesc }}
              </div>
            </div>
          </div>
          <div class="types" v-show="active == 1">
            <div class="song-list-box">
              <div class="song-list-top">
                <div class="song-list-left">
                  <div class="song-list-img-box">
                    <img class="auto-img" src="../assets/images/mv-play.png" />
                  </div>
                  <div class="song-list-playall">
                    播放全部<span>(热门{{ hotsingermusicdata.length }}首)</span>
                  </div>
                </div>
                <div @click="gosingallmusic" class="song-list-collect">
                  更多
                </div>
              </div>
              <div
                class="song-list-top"
                v-for="(item, index) in hotsingermusicdata"
                :key="index"
                @click="goPlayPage(item)"
              >
                <div class="song-list-left">
                  <div class="song-list-img-box">{{ index + 1 }}</div>
                  <div class="song-list-playall">
                    <div class="song-list-content">{{ item.name }}</div>
                    <div class="song-list-detail">
                      {{ item.ar[0].name }} - {{ item.al.name }}
                    </div>
                  </div>
                </div>
                <!-- <div class="song-list-collect">···</div> -->
                <div @click.stop="playmusic(item)" class="playbtn">
                  <van-icon name="play-circle-o" />
                </div>
              </div>
            </div>
          </div>
          <div class="types" v-show="active == 2">
            <div v-if="singeralbum.length > 0">
              <div class="song-list-box">
                <div
                  class="recommend-item"
                  v-for="(item, ind) in singeralbum"
                  :key="ind"
                  @click="goSongListDetails(item)"
                >
                  <div class="recommend-img-box">
                    <img class="auto-img" :src="item.picUrl" />
                  </div>
                  <div class="recommend-content">
                    <div class="recommend-name">{{ item.name }}</div>
                    <div class="recommend-text clearfix">
                      <div class="album fl">
                        {{
                          item.publishTime | getTemplateTimeByDate("YYYY.MM.dd")
                        }}
                      </div>
                      <div class="album fl">歌曲：{{ item.size }}</div>
                    </div>
                    <div v-show="item.artistName" class="recommend-text">
                      <div class="album">{{ item.artistName }}</div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div v-else>
              <van-empty description="暂无数据" />
            </div>
          </div>
          <div class="types" v-show="active == 3">
            <div v-if="singermvdata.length > 0">
              <div
                class="songer-mv-box clearfix"
                v-for="(item, index) in singermvdata"
                :key="index"
              >
                <div class="songer-mv-img fl">
                  <img class="auto-img" :src="item.imgurl16v9" alt="" />
                  <div class="songer-playcount">
                    <van-icon name="play-circle-o" />{{
                      item.playCount | million
                    }}
                  </div>
                </div>
                <div class="fl songer-mv-text">
                  <div class="songer-mv-name">{{ item.name }}</div>
                  <div class="songer-mv-time">{{ item.publishTime }}</div>
                </div>
              </div>
            </div>
            <div v-else>
              <van-empty description="暂无数据" />
            </div>
          </div>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import "../assets/less/singerdetail.less";
export default {
  data() {
    return {
      singerdetaildata: [],
      singermusicdata: [],
      hotsingermusicdata: [],
      singeralbum: [],
      singermvdata: [],
      active: 0,
      getsonglistid: 0,
      singertype: [
        {
          title: "主页",
        },
        {
          title: "歌曲",
        },
        {
          title: "专辑",
        },
        {
          title: "视频",
        },
      ],
    };
  },

  created() {
    this.getsonglistid = this.$route.query.id;
    this.getsongerdatas();
  },
  beforeRouteEnter(to, from, next) {
    if (to.name !== from.name) {
      next((vm) => {
        vm.getsonglistid = to.query.id;
      });
    }
  },
  watch: {
    getsonglistid(val) {
      if (val) {
        this.getsongerdatas();
      }
    },
  },
  filters: {
    million: function (value) {
      //播放量数值处理
      let num;
      if (value > 99999999) {
        num = Math.floor(value / 10000000) / 10 + "亿";
      } else if (value > 9999) {
        num = Math.floor(value / 1000) / 10 + "万";
      } else if (value < 9999 && value > -9999) {
        num = value;
      }
      return num;
    },
    // 处理播放时长
    getTemplateTimeByDate: function (date, template) {
      date = new Date(date);

      let TimeObj = {
        "Y+": date.getFullYear(),
        "(M+)": date.getMonth() + 1,
        "(d+)": date.getDate(),
        "(h+)": date.getHours(),
        "(m+)": date.getMinutes(),
        "(s+)": date.getSeconds(),
      };

      for (let k in TimeObj) {
        let reg = new RegExp(k);
        if (reg.test(template)) {
          let time = TimeObj[k]; // 判断当前模板时间是 两位 还是 一位的 // 如果是两位 个位数时间需要前面加零, 1 -> 01 // 如果是一位 不用加零操作
          if (RegExp.$1.length > 1) {
            time = time >= 10 ? time : "0" + time;
          }
          template = template.replace(reg, time);
        }
      }
      return template;
    },
  },
  methods: {
    //返回
    goback() {
      this.$router.go(-1);
    },
    getsongerdatas() {
      if (this.active == 0 || this.active == 1) {
        this.getsongerdetail();
      } else if (this.active == 2) {
        this.getsongeralbum();
      } else if (this.active == 3) {
        this.getsongermv();
      }
    },

    // 获取歌手详情和单曲
    getsongerdetail() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/artists",
        params: {
          id: this.getsonglistid,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code === 200) {
            this.singerdetaildata = result.data.artist;
            this.singermusicdata = result.data.hotSongs.slice(0, 8);
            this.hotsingermusicdata = result.data.hotSongs;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.error(err)
        });
    },
    // 获取歌手专辑
    getsongeralbum() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/artist/album",
        params: {
          // id: 31165848,
          id: this.getsonglistid,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code === 200) {
            this.singeralbum = result.data.hotAlbums;
          }
        })
        .catch((err) => {
          this.$toast.clear();
         console.error(err)
        });
    },
    // 获取歌手mv
    getsongermv() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/artist/mv",
        params: {
          // id: 31165848,
          id: this.getsonglistid,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code === 200) {
            this.singermvdata = result.data.mvs;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.error(err)
        });
    },

    //前往详情页面
    goPlayPage(item) {
      //前往并获取歌手歌单type = 3
      this.playmusicids = this.hotsingermusicdata.map((v) => {
        return v.id;
      });
      this.$store.commit("setplaymusicids", this.playmusicids);
      this.$store.commit("setmusicid", item.id);
    },
    //前往歌手全部歌曲页面
    gosingallmusic() {
      this.$router.push({
        path: "SingerAllMusic",
        query: { id: this.getsonglistid },
      });
    },

    playmusic(item) {
      this.playmusicids = this.hotsingermusicdata.map((v) => {
        return v.id;
      });
      this.$store.commit("setplaymusicids", this.playmusicids);
      this.$store.commit("setmusicid", item.id);
    },
    //前往歌单详情页面
    goSongListDetails(item) {
      this.$router.push({ path: "songAlbumDetails", query: { id: item.id } });
    },
  },
};
</script>